body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.3vw);
}

h2 {
  font-size: calc(1.2rem + 0.8vw) !important;
}

h3 {
  font-size: calc(1.1rem + 0.6vw) !important;
}

h4 {
  font-size: calc(0.75rem + 0.3vw) !important;
}

a {
  text-decoration: none;
  color: #212121;
}

p {
  font-size: calc(0.85rem + 0.3vw) !important;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.logo {
  margin-left: auto;
}
@media (min-width: 1200px) {
  .logo {
    margin-left: 0;
  }
}

.header {
  padding: 0;
}
.header div .logo {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
@media (min-width: 576px) {
  .header div .logo {
    flex-direction: row;
    margin-top: 0;
  }
}
.header div .logo h1,
.header div .logo span {
  font-size: calc(0.75rem + 0.25vw);
  text-align: center;
}
@media (min-width: 576px) {
  .header div .logo h1,
.header div .logo span {
    text-align: left;
  }
}
.header div .logo img {
  max-height: 7rem;
  padding: 0.5rem 0;
}
@media (min-width: 576px) {
  .header div .logo img {
    max-height: 6rem;
  }
}
.header div .sitename {
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.header div .sitename span {
  display: block;
  color: black;
}
@media (min-width: 576px) {
  .header div .sitename {
    margin-bottom: 0;
  }
}
.header div .navmenu a {
  text-transform: uppercase;
  font-weight: 700;
  padding: 0rem 15px;
  font-size: calc(0.6rem + 0.5vw);
}
@media (min-width: 1400px) {
  .header div {
    margin: auto 7.5vw;
  }
}

.main {
  margin-bottom: 5rem;
}
.main .hero {
  --background-color: transparent;
  min-height: 30vw;
  display: none;
}
@media (min-width: 576px) {
  .main .hero {
    display: block;
  }
}
.main .about {
  padding: 2rem 1rem;
  text-align: center;
}
.main .about .us-first-column {
  padding-right: 0;
  padding-left: 0;
}
@media (min-width: 1200px) {
  .main .about .us-first-column {
    padding-right: 4rem;
  }
}
@media (min-width: 576px) {
  .main .about {
    padding: 4rem inherit;
  }
}
@media (min-width: 1200px) {
  .main .about {
    padding: 6rem inherit;
    text-align: left;
  }
}
.main .services {
  padding: 8rem 1rem;
}
.main .services .service-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main .services .service-item i {
  margin-right: 0;
}
.main .services .service-item h4 {
  margin-top: 1rem;
}
.main .services .service-item h4,
.main .services .service-item .description {
  text-align: center;
}
@media (min-width: 576px) {
  .main .services .service-item {
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
  }
  .main .services .service-item i {
    margin-right: 2rem;
  }
  .main .services .service-item h4 {
    margin-top: 0;
  }
  .main .services .service-item h4,
.main .services .service-item .description {
    text-align: left;
  }
}
.main .services .service-item .icon {
  color: white;
}
.main .portfolio .section-title {
  padding-left: 1rem;
  padding-right: 1rem;
}
@media (min-width: 576px) {
  .main .portfolio .section-title {
    padding-left: 0;
    padding-right: 0;
  }
}
.main .portfolio .portfolio-content .portfolio-info .preview-link,
.main .portfolio .portfolio-content .portfolio-info .details-link {
  left: unset;
  top: unset;
  right: 10%;
  bottom: 15%;
}
.main .contact {
  padding: 1rem;
}
.main .contact .info-item {
  padding: 2rem;
}
.main .contact .info-item h3,
.main .contact .info-item a {
  text-align: center;
  font-size: calc(0.75rem + 0.25vw);
  display: inline-block;
  white-space: normal;
  overflow: visible;
  overflow-wrap: break-word;
  max-width: 100%;
}
.main .contact button {
  font-size: calc(0.75rem + 0.6vw);
}

.footer {
  padding-bottom: 0;
}
.footer .footer-top {
  padding-top: 2rem;
}
.footer .footer-about {
  text-align: center;
  padding: 2rem;
}
.footer .footer-about a {
  justify-content: center;
}
.footer .footer-about p {
  display: inline-block;
  white-space: normal;
  overflow: visible;
  overflow-wrap: break-word;
  max-width: 100%;
}
@media (min-width: 768px) {
  .footer .footer-about {
    text-align: left;
  }
  .footer .footer-about a {
    justify-content: left;
  }
  .footer .footer-about p {
    display: block;
  }
}
.footer .footer-links {
  text-align: center;
}
.footer .footer-links li {
  justify-content: center;
}
.footer .footer-links h4 {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .footer .footer-links {
    text-align: right;
  }
  .footer .footer-links li {
    justify-content: right;
  }
  .footer .footer-links h4 {
    margin-top: 2rem;
  }
}
.footer .footer-about .logo .logo-footer {
  max-height: 10rem;
}